                 </EraAccordion>\n                    <EraAccordion title=\"EditLogs\" :default-open=\"false\">\n                      <!-- 2 -->\n                      <template #default>\n                        <!--  -->\n                        <PrettyJsonViewer :value=\"dataRef.editLogs\" :default-collapsed=\"true\" :max-depth=\"2\" />\n                        <!-- JSON  -->\n                      </template>\n                    </EraAccordion>\n                  </div>\n                </template>\n              </EraAccordion>\n\n              <TabSwitch v-model:active=\"activeTab\" :tabs=\"tabs\">\n                <!--  -->\n                <template #pure>\n                  <!--  -->\n                  <PrettyJsonViewer :value=\"dataRef.statWithoutMeta\" :default-collapsed=\"true\" :max-depth=\"Infinity\" />\n                  <!-- JSON  -->\n                </template>\n                <template #full>\n                  <!--  -->\n                  <PrettyJsonViewer :value=\"dataRef.stat\" :default-collapsed=\"true\" :max-depth=\"Infinity\" />\n                  <!-- JSON  -->\n                </template>\n              </TabSwitch>\n            </template>\n\n            <template v-else>\n              <!--  -->\n              <div class=\"empty\"> era:writeDone </div>\n              <!--  -->\n            </template>\n          </div>\n        </div>\n\n        <!-- / -->\n        <aside class=\"right-rail\">\n          <!--  -->\n          <ActionButtons />\n          <!--  -->\n          <EraSettingsPanel />\n        </aside>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { computed, onMounted, ref, watch, onBeforeUnmount } from 'vue';\nimport { getScriptSettings } from '../utils/era_data';\nimport { Logger } from '../utils/log';\nimport ActionButtons from './components/ActionButtons.vue';\nimport EraSettingsPanel from './components/EraSettingsPanel.vue';\nimport FloatingBall from './components/FloatingBall.vue';\nimport ThemeManager from './components/ThemeManager.vue';\n\n//  EraDataPanel  imports\nimport EraAccordion from './components/era-panel/parts/EraAccordion.vue';\nimport MetaHeader from './components/era-panel/parts/MetaHeader.vue';\nimport PrettyJsonViewer from './components/era-panel/parts/PrettyJsonViewer.vue';\nimport TabSwitch from './components/era-panel/parts/TabSwitch.vue';\n\n//  EraDataPanel \ntype Actions = { rollback: boolean; apply: boolean; resync: boolean; api: boolean; apiWrite: boolean };\nexport interface WriteDonePayload {\n  mk: string;\n  message_id: number;\n  actions: Actions;\n  selectedMks: (string | null)[];\n  editLogs: Record<string, any[]>;\n  stat: any;\n  statWithoutMeta: any;\n  consecutiveProcessingCount: number;\n}\n\n// TabSwitch \ntype TabItem = { key: 'pure' | 'full'; label: string };\n\nconst logger = new Logger();\n\n// \nconst themeManager = ref<InstanceType<typeof ThemeManager> | null>(null);\nconst isDarkMode = ref(false);\n\nconst loadThemeSetting = () => {\n  try {\n    const settings = getScriptSettings();\n    const darkModeValue = settings?.[''] ?? false;\n    isDarkMode.value = darkModeValue;\n    if (themeManager.value) {\n      themeManager.value.isDarkMode = darkModeValue;\n    }\n    logger.log('loadThemeSetting', `: ${darkModeValue ? 'Dark' : 'Light'}`);\n  } catch (e) {\n    logger.error('loadThemeSetting', '', e);\n    isDarkMode.value = false; // Fallback to light mode\n  }\n};\n\nonMounted(() => {\n  loadThemeSetting();\n  window.addEventListener('era-settings-updated', loadThemeSetting);\n});\n\nonBeforeUnmount(() => {\n  window.removeEventListener('era-settings-updated', loadThemeSetting);\n});\n\n// App.vue  props\nconst props = defineProps({\n  initialView: {\n    type: String,\n    required: true,\n    default: 'FloatingBall',\n  },\n  eventData: {\n    type: Object as () => WriteDonePayload | null,\n    default: () => null,\n  },\n});\n\n// App.vue \nconst currentComponent = ref(props.initialView);\nconst requestSwitchView = (viewName: 'FloatingBall' | 'ExpandedView') => {\n  logger.debug('requestSwitchView', `: ${viewName}`);\n  if ((window as any).eraUiSwitchView) {\n    (window as any).eraUiSwitchView(viewName);\n  } else {\n    logger.warn('requestSwitchView', ' eraUiSwitchView ');\n  }\n};\n\n//  EraDataPanel \nconst dataRef = computed(() => props.eventData || null);\n\nconst tabs: TabItem[] = [\n  { key: 'pure', label: '' },\n  { key: 'full', label: '' },\n];\nconst activeTab = ref<'pure' | 'full'>('pure');\n\nwatch(\n  () => props.eventData,\n  (newData, oldData) => {\n    logger.debug('watch:eventData', 'eventData prop ', {\n      newData,\n      oldData,\n    });\n  },\n  { deep: true },\n);\n\nwatch(\n  () => props.initialView,\n  newView => {\n    logger.debug('watch:initialView', `initialView prop : ${newView}`);\n    currentComponent.value = newView;\n  },\n);\n</script>\n\n<style>\n/* App.vue  .era-app-container */\n.era-app-container {\n  /*  */\n  position: fixed; /*  */\n  inset: 0; /* top/right/bottom/left  0 */\n  z-index: 2147483646; /*  */\n  pointer-events: none; /*  */\n  isolation: isolate; /*  */\n  contain: layout style paint; /*  */\n  width: 100svw; /*  */\n  height: 100svh; /*  */\n}\n</style>\n\n<style scoped>\n/*  EraDataPanel.vue  */\n.era-panel {\n  width: min(960px, 60vw);\n  height: min(680px, 86vh);\n  display: flex;\n  flex-direction: column;\n  background: var(--bg-blur-heavy);\n  border: 1px solid var(--border-strong);\n  backdrop-filter: blur(10px);\n  border-radius: 16px;\n  box-shadow: var(--shadow-panel), var(--shadow-inset);\n  overflow: hidden;\n  font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, monospace;\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n\n.panel-top {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 14px 16px;\n  border-bottom: 1px solid var(--border-soft);\n  background: var(--bg-blur-light);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n.panel-title {\n  font-size: 16px;\n  font-weight: 800;\n  letter-spacing: 0.5px;\n  color: var(--text-title);\n  transition: color 0.3s ease;\n}\n.btn-close {\n  width: 32px;\n  height: 32px;\n  line-height: 30px;\n  text-align: center;\n  border-radius: 8px;\n  border: 1px solid var(--border-normal);\n  background: var(--bg-solid);\n  cursor: pointer;\n  font-size: 18px;\n  color: var(--text-normal);\n  box-shadow: var(--shadow-button);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease,\n    transform 0.2s ease,\n    box-shadow 0.2s ease;\n}\n.btn-close:hover {\n  transform: translateY(-1px);\n  box-shadow: var(--shadow-button-hover);\n}\n\n.panel-body {\n  padding: 14px 14px 16px;\n  overflow: auto;\n}\n\n.block {\n  margin: 12px 0 4px;\n  padding: 10px 12px;\n  background: var(--bg-soft);\n  border: 1px solid var(--border-soft);\n  border-radius: 12px;\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n.block-title {\n  margin: 0 0 8px;\n  font-size: 13px;\n  font-weight: 800;\n  color: var(--text-subtitle);\n  transition: color 0.3s ease;\n}\n\n.chips {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n}\n.chip {\n  font-size: 12px;\n  padding: 6px 8px;\n  border: 1px solid var(--chip-border);\n  border-radius: 999px;\n  background: var(--chip-bg);\n  color: var(--chip-text);\n  box-shadow: var(--shadow-inset);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease;\n}\n.chip.ok {\n  color: var(--chip-ok-text);\n  background: var(--chip-ok-bg);\n  border-color: var(--chip-ok-border);\n}\n\n.mk-strip {\n  display: flex;\n  gap: 8px;\n  flex-wrap: wrap;\n  max-height: 140px;\n  overflow: auto;\n}\n.mk-pill {\n  display: inline-flex;\n  gap: 6px;\n  align-items: center;\n  padding: 6px 8px;\n  background: var(--mk-pill-bg);\n  color: var(--mk-pill-text);\n  border: 1px solid var(--mk-pill-border);\n  border-radius: