  right: max(16px, env(safe-area-inset-right)); /*  +  */\n  bottom: max(90px, env(safe-area-inset-bottom)); /*  +  */\n  z-index: 2147483647; /*  */\n  pointer-events: auto; /*  none */\n  touch-action: manipulation; /*  */\n  will-change: transform; /*  3D/ */\n}\n\n/* ===[]  ExpandedView v-show  true  === */\n.era-expanded-layer {\n  /*  class */\n  position: fixed; /*  */\n  inset: 0; /*  */\n  display: grid; /*  grid  */\n  place-items: center; /*  */\n  padding: clamp(12px, 2.5vw, 24px); /*  */\n  pointer-events: auto; /*  none  */\n  z-index: 2147483646; /*  */\n  background: transparent; /*  rgba(0,0,0,.25) */\n  overflow: auto; /*  */\n}\n\n/* ===[]  === */\n@media (max-width: 640px) {\n  /*  */\n  .era-panel {\n    /*  */\n    width: min(92vw, 560px); /*  92vw 60vw  */\n    height: min(88vh, calc(100svh - 32px)); /* / */\n  }\n  .panel-body {\n    /*  */\n    overscroll-behavior: contain; /*  */\n  }\n}\n</style>\n"],"sourceRoot":""}]);
